/* App.css */
.App-header {
  background-color: #282c34;
  min-height: 85vh;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  padding: 20px;
  text-align: center;
}

.image-container {
  float: right;
  margin-right: 100px;
}

.image-container img {
  width: 600px;
  height: auto;
  border-radius: 50%;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.4);
}

.text-container {
  text-align: left;
  margin-left: 100px;
}

.text-container h1, .text-container p {
  margin: 0;
  margin-bottom: 30px;
  font-weight: bold;
}

/* Awwward-winning style for the highlighted name */
.text-container .highlight {
  color: #ff0055;
  font-size: 1.2em;
  font-weight: bold;
}

/* Awwward-winning style for the menu */
.App-menu {
  background-color: #333;
  padding: 20px;
  position: sticky;
  top: 0;
}

.App-menu ul {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  justify-content: center;
}

.App-menu li {
  margin: 0 10px;
}

.App-menu a {
  color: #fff;
  text-decoration: none;
  font-size: 16px;
  font-weight: bold;
  padding: 10px;
  transition: color 0.3s ease;
}

.App-menu a:hover {
  color: #61dafb;
}

/* Main section */
#about {
  background-color: #f7f7f7;
  padding: 50px;
}

#about h2 {
  font-size: 32px;
  margin-bottom: 20px;
  color: #333;
}

#about p {
  margin-bottom: 20px;
  color: #666;
}

#about a {
  color: #0066cc;
}

